<template>
    <div class="chart">
        <div class="chart_content">
            <div class="message_area">
                <div>
                    <div v-for='msg in allMessage'>
                        <System
                        v-if='msg.user'
                        :name = 'msg.user'
                        :time = 'msg.time'
                        />
                        <MyMessage
                        v-if='msg.name === name && !msg.user'
                        :name = 'msg.name'
                        :message = 'msg.message'
                        :time = 'msg.time'
                        :img = 'msg.src'
                        />
                        <Message
                        v-if='msg.name !== name && !msg.user' 
                        :name = 'msg.name'
                        :message = 'msg.message'
                        :time = 'msg.time'
                        :img = 'msg.src'
                        />
                    </div>
                </div>
            </div>
            <div class="chart_footer">
                <div class="chart_information">
                    <img src="../assets/User shape free icon.svg" alt="icon">
                    <span>{{ online }}/99</span>
                    <img src="../assets/Padlock free icon.svg" alt="icon">
                    <span>私密</span>
                    <img src="../assets/Cog wheel silhouette free icon.svg" alt="icon">
                </div>
                <div class="input_text">
                    <span>{{ name }} > </span>
                    <!-- <textarea @keydown="sendMessage" v-model='message'></textarea> -->
                    <input type="text" @keydown="sendMessage" v-model='message'>
                    <div>
                        <span class="chart_featur">
                            <label>
                                <img src="../assets/Paper clip outline free icon.svg" alt="icon">
                                <input type="file" @click="sandFile">
                            </label>
                            <label>
                                <img src="../assets/Picture free icon.svg" alt="icon">
                                <input type="file" id="img" @change="sandImg">
                            </label>
                            <span @click.stop="useEmoji">(ﾟ∀ﾟ)
                                <div class="emoji" v-if='emoji'>
                                    <div 
                                    v-for='(emj,index) in emojiBox' 
                                    :key='emj'
                                    @click='selectEmj(index)'
                                    >
                                    {{ emj }}
                                    </div>
                                </div>
                            </span>
                        </span>
                        <button>傳送 ></button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import MyMessage from '@/components/MyMessage.vue';
import Message from '@/components/Message.vue';
import System from '@/components/System.vue';

export default {
    name: 'room',
    components: {
        MyMessage,
        Message,
        System
    },
    data(){
        return {
            online: 1,
            message: '',
            allMessage: [],
            emoji: false,
            emojiBox: [
                'σ`∀´)σ', '_(:3 」∠ )_', '(^_っ^)',
                '(́◉◞౪◟◉‵)', 'థ౪థ', '(σ′▽‵)′▽‵)σ',
                '｡:.ﾟヽ(*´∀`)ﾉﾟ.:｡', '(◔౪◔)', '(*´∀`)~♥',
                '(*ﾟ∀ﾟ*)', '(`・ω・´)', '。･ﾟ･(つд`ﾟ)･ﾟ･'
            ],
            img: []
        }
    },
    computed: {
        name(){
            return this.$store.state.user;
        }
    },
    sockets: {
        getHistory: function(data){
            this.allMessage = data;
        },
        checkOnline: function(online){
            this.online = online;
        },
        setMessage: function(obj){
            this.allMessage.push(obj);
        },
        getUser: function(user){
            let hour = new Date().getHours();
            let minute = new Date().getMinutes();
            minute < 10 ? minute = '0' + minute : minute;
            let nowTimer = `${hour}:${minute}`;
            this.allMessage.push({
                user: user,
                time: nowTimer,
            });
        },
        getImg: function(data){
            this.allMessage.push(data);
        }
    },
    methods: {
        sendMessage(e){
            if(e.keyCode === 13){
                let hour = new Date().getHours();
                let minute = new Date().getMinutes();
                minute < 10 ? minute = '0' + minute : minute;
                let nowTimer = `${hour}:${minute}`;
                let obj = {
                    name: this.name,
                    message: this.message,
                    time: nowTimer,
                }

                this.$socket.emit('sendMessage', obj);
                this.message = '';

                let scrollArea = document.querySelector('.message_area>div');
                scrollArea.scrollTop = scrollArea.scrollHeight;
            }
        },
        useEmoji(){
            this.emoji ? this.emoji = false : this.emoji = true;
        },
        selectEmj(index){
            this.message = this.emojiBox[index];
        },
        sandImg(){
            let hour = new Date().getHours();
            let minute = new Date().getMinutes();
            minute < 10 ? minute = '0' + minute : minute;
            let nowTimer = `${hour}:${minute}`;

            let data = {
                name: this.name,
                time: nowTimer
            }

            let file = document.getElementById('img');
            let vm = this;
            // console.log(file)
            let imgFile = file.files[0];
            // console.log(imgFile)
            const reader = new FileReader();
            if(imgFile){
                reader.readAsDataURL(imgFile);
            }
            // console.log(reader);
            reader.onload = function(){
                data.src = this.result;
                console.log(data);
                vm.$socket.emit('sendImg', data)
            }
        },
        sandFile(){
            alert('功能尚未完成');
        }
    }

}
</script>

<style lang="scss" scoped>
    $bg_color: #1E1E1E;
    $bg2_color: #303030;
    $emoji_bg: #434343;
    $text_color: #D4D4D4;
    $emoji_hover: #D7BA7D;

    .clearfix::after{
        content: '';
        display: block;
        clear: both;
    }
    .chart_content {
        height: 100vh;
        padding-top: 67px;
        position: relative;
    }
    .message_area {
        width: 100%;
        height: 60vh;
        overflow: hidden;
        >div {
            width: 100%;
            height: 99%;
            overflow: auto;
            padding-right: 17px;
            box-sizing: content-box;
        }
    }
    .chart_footer {
        position: absolute;
        bottom: 0;
        width: 100%;
        min-height: 30vh;
        background-color: $bg_color;
        z-index: 1;
    }
    .chart_information {
        width: 100%;
        height: 31px;
        line-height: 31px;
        background-color: $bg2_color;
        color: $text_color;
        text-align: right;
        padding: 0 12px;
        span {
            margin: 0 15px 0 6px;
        }
    }
    .input_text {
        padding: 13px 0 0 75px;
        input {
            vertical-align: top;
            width: 94%;
            height: 150px;
            background-color: $bg_color;
            color: $text_color;
            border: none;
            outline: none;
        }
        >div {
            width: 100%;
            height: 30px;
            display: flex;
            justify-content: space-between;
        }
        button {
            float: right;
            width: 140px;
            height: 30px;
            line-height: 30px;
            border: none;
            background-color: $bg2_color;
            color: $text_color;
            outline: none;
            cursor: pointer;
        }
    }
    .chart_featur {
        img {
            margin-right: 20px;
            cursor: pointer;
        }
        span {
            cursor: pointer;
            position: relative;
        }
        input {
            display: none;
        }
    }
    .emoji {
        position: absolute;
        bottom: 30px;
        left: -50px;
        width: 445px;
        height: 200px;
        background-color: $emoji_bg;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        >div{
            text-align: center;
            color: $text_color;
            width: 33.3333%;
            font-size: 1.125rem;
            &:hover {
                color: $emoji_hover;
            }
        }
    }
</style>