<template>
    <div class="people_talk clearfix">
        <p>{{ time }}</p>
        <div>
            <p><<span style="color: #CE9178">{{ name }}</span><span>></span></p>
            <p class="text" style="color: #D4D4D4">{{ message }}</p>
            <div>
                <img :src="img" alt="pic" v-if='img' width="300">
            </div>
            <span><<span>/<span style="color: #CE9178">{{ name }}</span></span></span>> 
        </div>
    </div>
</template>

<script>
export default {
    name: 'message',
    props: {
        name: {
            type: String,
            require: true
        },
        message: {
            type: String,
            require: true
        },
        time: {
            type: String,
            require: true
        },
        img: {
            type: String,
            require: true
        }
    }
}
</script>

<style lang="scss" scoped>
    $bg_color: #1E1E1E;
    $bg2_color: #303030;
    $text_color: #D4D4D4;
    .clearfix::after{
        content: '';
        display: block;
        clear: both;
    }
    .people_talk {
        margin: 0 0 11px 75px;
        >p {
            float: left;
        }
        >div {
            float: left;
            margin-left: 5px;
        }
        .text {
            margin-left: 40px;
            position: relative;
            &::before{
                content: '';
                position: absolute;
                top: 0;
                left: -20px;
                width: 1px;
                height: 21px;
                background-color: rgba(212, 212, 212, 0.3);
            }
        }
    }
</style>